import React, { useState } from "react";
import styled from "styled-components";
import Container from "react-bootstrap/Container";
import { Form } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import axios from "axios";

const ProfileCard = () => {
  const [email, setEmail] = useState("");
  const [userData, setUserData] = useState(null);

  const handleGetUserByEmail = async () => {
    try {
      const response = await axios.get(
        `${process.env.REACT_APP_API_URL}api/user`
      );
      setUserData(response.data[0]);
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <>
      <Container
        className="d-flex justify-content-center align-items-center"
        style={{ height: window.innerHeight - 54 }}
      >
        <Card className="mt-2 row justify-content-center">
          <Form.Control
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="  "
          />
          <Button variant={"outline-success"} onClick={handleGetUserByEmail} style={{marginTop: "10px"}}>
             
          </Button>
          {userData && (
            <>
              <Title></Title>
              <Field>
                <Label>:</Label>
                <Value>{userData.name || ""}</Value>
              </Field>
              <Field>
                <Label>:</Label>
                <Value>{userData.surname || ""}</Value>
              </Field>
              <Field>
                <Label>:</Label>
                <Value>{userData.email || ""}</Value>
              </Field>
              <Field>
                <Label>:</Label>
                <Value>{userData.address || ""}</Value>
              </Field>
              <Field>
                <Label> :</Label>
                <Value>{userData.num_tel || ""}</Value>
              </Field>
            </>
          )}
        </Card>
      </Container>
    </>
  );
};

const Card = styled.div`
  background-color: white;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 400px;
`;

const Title = styled.h2`
  font-size: 24px;
  margin-bottom: 20px;
`;

const Field = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
`;

const Label = styled.span`
  font-weight: bold;
  margin-bottom: 5px;
`;

const Value = styled.span`
  color: #666;
`;

export { ProfileCard };
